@import './colors';
@import './buttons';

.forms {
    background-color: $gray-color;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    
   

    form {
        color: $main-color;
        font-size: 20px;
        text-align: center;
        display: block;
        width: 50%;
    }

    label {
        text-transform: uppercase;
    }

    input {
        width:  100%;
        padding: 1rem;
        margin: 1rem;
    }

    button {
        @include button;
    }
    button:hover {
        @include hover;
    }
}